body {
  padding: 5px 30px 30px;
}

.container {
  width: 1300px;
  margin: 0 auto;
  padding: 20px;
}

.form-styles {
  background-color: #6c40b9;
  padding: 20px 500px;
}

.search-form {
  flex-direction: row;
  display: flex;
}

.gallery {
  flex-flow: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  flex-direction: column;
  font-size: 15px;
}

.info {
  flex-direction: row;
  gap: 20px;
  display: flex;
}

.info-item {
  text-align: center;
}

.load-more.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

